.mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  max-width: 1024px;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.3);
}
.dialog_content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 20%;
  z-index: 2;
  height: 100%;
  display: none;
  background-color: #FFF;
  transition: transform 0.2s linear;
}
