.wrapper {
  height: 100%;
  overflow: hidden;
}
.filter_wrapper > li:first-child {
  padding: 8px 10px;
  background-color: #F7F7F7;
}
.filter_wrapper > li:last-child {
  padding: 10px 0 10px 10px;
}
.filter_wrapper > li:last-child .filter_item_wrapper > li {
  display: inline-block;
  vertical-align: top;
  width: 25%;
  padding: 10px 0;
  margin: 0 8.33% 10px 0;
  text-align: center;
  border: 1px solid #DDD;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.filter_wrapper > li:last-child .filter_item_wrapper .active {
  border-color: #FF0000;
  color: #FF0000;
}
