.wrapper {
  display: flex;
  align-items: center;
  padding: 0 8px;
  text-align: center;
  color: #AEAEAE;
  position: relative;
}
@media screen and (max-width: 321px) {
  .wrapper {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper {
    /*大*/
    font-size: 17px;
  }
}
.wrapper::before,
.wrapper::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper::before {
  display: none;
  top: 0;
}
.wrapper::after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper::before,
  .wrapper::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper::before,
  .wrapper::after {
    transform: scaleY(0.33);
  }
}
.wrapper > li {
  flex: 1;
}
.wrapper > li > div {
  position: relative;
}
.wrapper > li > div::before,
.wrapper > li > div::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  border-left: 1px dashed #DDD;
  height: 100%;
  top: 0;
  bottom: 0;
}
.wrapper > li > div::before {
  display: none;
  left: 0;
}
.wrapper > li > div::after {
  right: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper > li > div::before,
  .wrapper > li > div::after {
    transform: scaleX(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper > li > div::before,
  .wrapper > li > div::after {
    transform: scaleX(0.33);
  }
}
.wrapper > li::after {
  content: '';
  display: block;
  width: 50%;
  height: 1px;
  margin: 4px auto auto auto;
  background-color: transparent;
}
.wrapper > li:last-child > div::after {
  display: none;
}
.wrapper .active {
  color: #FF0000;
}
.wrapper .active::after {
  background-color: #FF0000;
}
